}

.search-bar {
  background-color: #b8aed1;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

input {
  color: #333;
  width: 90%;
  background-color: #fff;
  border: none;
  border-bottom: .3rem solid rgba(0, 0, 0, 0);
  border-radius: .2rem;
  margin: 0 auto;
  padding: .5rem 2rem;
  font-family: Roboto, sans-serif;
  font-size: 1.2rem;
  transition: all .3s;
  display: block;
}

.search-form {
  gap: 20px;
  display: flex;
}

button {
  all: unset;
  width: 100px;
  height: 30px;
  color: #f0f0f0;
  cursor: pointer;
  z-index: 1;
  white-space: nowrap;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  font-size: 16px;
  display: flex;
  position: relative;
}

button:after, button:before {
  content: "";
  z-index: -99999;
  transition: all .4s;
  position: absolute;
  bottom: 0;
  right: 0;
}

button:before {
  width: 100%;
  height: 100%;
  background: #28282d;
  border-radius: 10px;
  transform: translate(0%);
}

button:after {
  width: 35px;
  height: 35px;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  background: rgba(255, 255, 255, .082);
  border-radius: 50px;
  transform: translate(10px, 10px);
}

button:hover:before {
  width: 110%;
  height: 110%;
  transform: translate(5%, 20%);
}

button:hover:after {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  transform: translate(0);
}

button:active:after {
  transition: all;
  transform: translate(0, 5%);
}

.load-more {
  margin: 30px auto;
  display: flex;
}

.hide {
  display: none;
}

